import { Injectable } from '@nestjs/common';

import { DecodedToken } from '../entity';
import { BearerToken, PersonnelId } from '../value';

@Injectable()
export abstract class AuthorizationRepository {
  abstract verifyTokenToDecodedToken(token: BearerToken): Promise<DecodedToken>;
  abstract updatePersonnelId(
    uid: string,
    personnelId: PersonnelId,
  ): Promise<void>;
}
